* {
    background-color: wheat;
}

.App {
    margin: 0 auto;
    padding: 20px;
    text-align: center;
    background-color: wheat;
    min-height: 100vh;
}

.App-header {
    background-color: #282c34;
    min-height: 10vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

h1 {
    font-size: 3rem;
    color: whitesmoke;
    text-shadow: 2px 2px #666;
    margin: 0;
    background-color: #282c34;
    font-family: 'Poppins', sans-serif;
}

.show-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
    justify-items: center;
}

.show-summary {
    width: 100%;
    max-width: 500px;
    margin-bottom: 40px;
    padding: 20px;
    background-color: wheat;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: left;
    transition: transform 0.2s ease-in-out;
}

.show-summary:hover {
    transform: translateY(-5px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    background-color: coral;
    color: aliceblue;
}

.show-summary:hover * {
    background-color: coral;
    color: azure;
}

.show-image img {
    max-width: 100%;
    height: auto;
    border-radius: 4px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.show-info {
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
}

.show-info h2 {
    margin-bottom: 10px;
    color: #282c34;
    font-size: 1.5rem;
    font-family: 'Poppins', sans-serif;
}

.show-info p {
    margin: 10px 0;
    color: #555;
}

@media screen and (max-width: 600px) {
    .show-list {
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    }
}

@media screen and (max-width: 400px) {
    .App {
        padding: 10px;
    }
    .show-summary {
        padding: 10px;
    }
}